<template>
  <div class="drag-list-wrapper">
    <Hints>
      <template slot="hintName"></template>
      <template slot="hintInfo">
        <p>Vue.DraggableSortable.jsvue</p>
        <p>github <el-link type="success" href="https://github.com/SortableJS/Vue.Draggable" target="_blank">Vue.Draggable</el-link></p>
      </template>
    </Hints>
    <el-row :gutter="20">
      <el-col :span="5">
        <el-card shadow="always">
          <div slot="header" class="title">1</div>
          <Draggable v-model="listOne" group="list" @end="handleChange($event, '1')" class="list-cont list1">
            <el-card v-for="(item, index) in listOne" :key="index" shadow="hover">{{ item.name }}</el-card>
          </Draggable>
        </el-card>
      </el-col>
      <el-col :span="5">
        <el-card shadow="always">
          <div slot="header" class="title">2</div>
          <Draggable v-model="listTwo" group="list" @end="handleChange($event, '2')" class="list-cont list2">
            <el-card v-for="(item, index) in listTwo" :key="index" shadow="hover">{{ item.name }}</el-card>
          </Draggable>
        </el-card>
      </el-col>
      <el-col :span="4">
        <el-card shadow="always">
          <div slot="header" class="title"></div>
          <div class="list-cont">
            <p v-for="(item, index) in handleList" :key="index">{{ item }}</p>
          </div>
        </el-card>
      </el-col>
      <el-col :span="5">
        <el-card shadow="always">
          <div slot="header" class="title">1</div>
          <pre class="list-cont">{{ listOne }}</pre>
        </el-card>
      </el-col>
      <el-col :span="5">
        <el-card shadow="always">
          <div slot="header" class="title">2</div>
          <pre class="list-cont">{{ listTwo }}</pre>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Draggable from 'vuedraggable'
import Hints from '../../../components/Hints'
const message = [
  'ECMAScript6',
  'VueJS',
  'ReactJS',
  'AngularJS',
  'Webpack',
  'NodeJS',
  'TypeScript'
]

export default {
  name: 'DragList',
  data () {
    return {
      listOne: message.map((name, index) => {
        return {name, order: index + 1}
      }),
      listTwo: [],
      handleList: []
    }
  },
  components: { Draggable, Hints },
  methods: {
    handleChange (event, type) {
      const srcClassName = event.from.classList[1]
      const targetClassName = event.to.classList[1]
      let from = ''
      let to = ''
      if (srcClassName === targetClassName) {
        if (type === '1') {
          from = '1'
          to = '1'
        } else {
          from = '2'
          to = '2'
        }
      } else {
        if (type === '1') {
          from = '1'
          to = '2'
        } else {
          from = '2'
          to = '1'
        }
      }
      this.handleList.push(`${from} => ${to}, ${event.oldIndex} => ${event.newIndex}`)
    }
  }
}
</script>

<style lang="less">
.drag-list-wrapper {
  .title {
    text-align: center;
    font-size: 14px;
    font-weight: bold;
  }
  .list-cont {
    height: 510px;
    .el-card {
      margin-bottom: 10px;
      .el-card__body {
        padding: 14px 20px;
      }
    }
  }
  pre {
    font-family: Consolas,Menlo,Courier,monospace;
  }
}
</style>



